/* src/styles/App.css */
body {
  margin: 0;
  font-family: 'Segoe UI', sans-serif;
  background: #f5f7fa;
  color: #333;
}
.app-container {
  max-width: 1200px;
  margin: 2rem auto;
  padding: 0 1rem;
}
h1 {
  text-align: center;
  margin-bottom: 1.5rem;
}
.filter-bar {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 1rem;
}
.filter-bar label {
  display: flex;
  flex-direction: column;
  font-size: 0.9rem;
}
.filter-bar input,
.filter-bar select {
  padding: 0.4rem;
  border-radius: 4px;
  border: 1px solid #ccc;
}
